import { Observable } from 'rxjs/Observable';
import {
    HttpInterceptor, HttpRequest, HttpHandler,
    HttpSentEvent, HttpHeaderResponse, HttpProgressEvent,
    HttpResponse, HttpUserEvent
} from '@angular/common/http';

import { Injectable } from '@angular/core';
import { IvtHttpClientConfig } from '../config';
import { map } from 'rxjs/operators';

@Injectable()
export class IvtHttpInterceptor implements HttpInterceptor {

    constructor(private config: IvtHttpClientConfig) {
    }
    intercept(req: HttpRequest<any>, next: HttpHandler):
        Observable<HttpSentEvent | HttpHeaderResponse | HttpProgressEvent | HttpResponse<any> | HttpUserEvent<any>> {
        let clonedReq: any = null;
        if (req.params.get('auth') !== 'false') {
            clonedReq = clonedReq || {};
            clonedReq.headers = req.headers.set('Authorization', this.config.getAuthorizationHeader());
        }
        if (req.method === 'POST') {
            const transformedBody = this.config.transformRequestBody(req);
            if (transformedBody != null) {
                clonedReq = clonedReq || {};
                clonedReq.body = transformedBody;
            } else {
                console.log('[IvtHttpInterceptor] transformRequest() is null, request body is not transformed');
            }
        }
        if (clonedReq != null) {
            req = req.clone(clonedReq);
        }
        return next.handle(req).pipe(map(response => {
            if (response instanceof HttpResponse) {
                const transformedBody = this.config.transformResponseBody(response);
                if (transformedBody != null) {
                    response = response.clone({
                        body: transformedBody
                    })
                } else {
                    console.log('[IvtHttpInterceptor] transformResponse() is null, response body is not transformed');
                }
            }
            return response;
        }))
    }

}
