import { plainToClass } from 'class-transformer';
import { Observable } from 'rxjs/Observable';
import { catchError, map } from 'rxjs/operators';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { ErrorObservable } from 'rxjs/observable/ErrorObservable';
import 'rxjs/add/observable/throw';
import { APIResponse } from '../model/api.response';
import { APICode } from '../constant/api.code';
import { IvtHttpClientConfig } from '../config';

export class IvtHttpService<T> {

    constructor(
        public httpClient: HttpClient,
        protected url: string,
        private ClazzType: Type<T>,
        private config: IvtHttpClientConfig) {
    }

    public extractData(res: APIResponse): T | T[] {
        const response = <APIResponse>(res);
        if (response.code === APICode.Success) {
            if (response.data instanceof Array) {
                return plainToClass<T, T[]>(this.ClazzType, response.data);
            } else {
                return plainToClass<T, T>(this.ClazzType, response.data);
            }
        }
        throw response;
    }

    protected handleError(error: HttpErrorResponse) {
        let response = new APIResponse();
        if (error.error instanceof ErrorEvent) {
            // A client-side or network error occurred. Handle it accordingly.
            console.error('[ServicesBase]: An error occurred:', error.error.message);
            response.code = APICode.ClientError;
            response.message = error.error.message;
        } else if (error instanceof APIResponse) {
            return Observable.throw(error);
        } else if (error instanceof Error) {
            response.code = APICode.ClientError;
            response.message = error.message;
        } else if (error.error instanceof ProgressEvent) {
            response.code = APICode.Progress;
            response.event = error.error;
        } else {
            // The backend returned an unsuccessful response code.
            // The response body may contain clues as to what went wrong,
            const transformedResponse = this.config.transformResponseBody(error);
            if (transformedResponse) {
                response = transformedResponse;
            } else {
                // transformResponse is not implemented
                response = error.error;
            }
            response.code = error.status.toString();
            console.error(
                `[ServicesBase]: Backend returned code ${response.code}, ` +
                `body was: ${response.message}`);
        }
        return Observable.throw(response);
    };

    getAll(parentId?: string): Observable<T[]> {
        return this.httpClient.get<APIResponse>(this.url + (parentId ? `/${parentId}` : ''))
            .pipe(map(res => this.extractData(res)))
            .pipe(catchError(err => this.handleError(err)));
    }

    get(id: string): Observable<T> {
        return this.httpClient.get<APIResponse>(this.url + (id ? `/${id}` : ''))
            .pipe(map(res => this.extractData(res)))
            .pipe(catchError(err => this.handleError(err)));
    }

    // create or update
    save(item: any): Observable<T> {
        return this.httpClient.post<APIResponse>(this.url, item)
            .pipe(map(res => this.extractData(res)))
            .pipe(catchError(err => this.handleError(err)));
    }

    search(criteria?: any): Observable<T[]> {
        return this.httpClient.post<APIResponse>(this.url, criteria)
            .pipe(map(res => this.extractData(res)))
            .pipe(catchError(err => this.handleError(err)));
    }

    delete(id: string): Observable<any> {
        return this.httpClient.delete<APIResponse>(this.url + (id ? `/${id}` : ''))
            .pipe(map(res => this.extractData(res)))
            .pipe(catchError(err => this.handleError(err)));
    }

}

// tslint:disable-next-line:interface-over-type-literal
export declare type Type<T> = {
    new(...args: any[]): T;
}
