import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HTTP_INTERCEPTORS, HttpClientModule } from '@angular/common/http';
import { IvtHttpInterceptor } from './service/interceptor';
import { IvtHttpClientConfig } from './config';
import { APICode } from './constant/api.code';
import { APIResponse } from './model/api.response';
import { ErrorLevel } from './constant/error.level';
import { IvtHttpService } from './service/base.service';
// Export module's public API

export { APICode } from './constant/api.code';
export { ErrorLevel } from './constant/error.level';
export { APIResponse } from './model/api.response';
export { IvtHttpInterceptor } from './service/interceptor';
export { IvtHttpClientConfig } from './config';
export { IvtHttpService } from './service/base.service';


@NgModule({
  imports: [
    CommonModule,
    HttpClientModule
  ]
})
export class IvtHttpClientModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: IvtHttpClientModule,
      providers: [
        IvtHttpClientConfig,
        { provide: HTTP_INTERCEPTORS, useClass: IvtHttpInterceptor, multi: true }
      ]
    };
  }
}
